import Page from '../components/Page';
import React from 'react';
import { history } from "App.js";
import { Card, Col, Row,
    Button,
    CardBody,
    CardHeader,
    FormGroup,
    Input,
    Label,
    UncontrolledAlert
} from 'reactstrap';
import connect from "react-redux/es/connect/connect";
import 'froala-editor/js/froala_editor.pkgd.min.js';
// Require Editor CSS files.
import 'froala-editor/css/froala_style.min.css';
import 'froala-editor/css/froala_editor.pkgd.min.css';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';
import { getListCompanies } from '../store/actions/companies'
import {putBroker} from '../store/actions/broker';
import Select from 'react-select';
// Require Font Awesome.
// import 'font-awesome/css/font-awesome.css';


const createSchema = Yup.object().shape({
    name: Yup.string()
        .required('Mi tn'),
    email: Yup.string()
        .email('Email sai nh dng'),
    password: Yup.string()
        .min(6,'Password nh nht l 6 k t')
        .max(32, 'Password ln nht l 32 k t')
        .nullable(),
    phone: Yup.string()
        .matches(/^([0-9])\d{9,11}$/,{
            message: 'in thoi sai nh dng'
        }).required('Mi nhp s in thoi'),
    introduce: Yup.string().nullable(),
    dob: Yup.date('Ngy sinh nht phi l ngy').nullable(),
    repassword: Yup.string()
        .oneOf([Yup.ref('password'), null], 'Password khng khp')

});
class BrokerEditPage extends React.Component {

    constructor(props) {
        super(props);
        this.props.getListCompanies();
    }

    render() {
        let options = []
        if(this.props.companies.docs){
            this.props.companies.docs.forEach((company)=>{
                options.push({value:JSON.stringify({
                        id: company._id,
                        name: company.name
                    }),label:company.name})
            })

        }
        let errMsg;
        if(this.props.error && this.props.error.errorMsg){
            errMsg = <UncontrolledAlert color="danger">
                {this.props.error.errorMsg}
            </UncontrolledAlert>
        }
        return (
            <Page
                title="To ngi mi gii"
                breadcrumbs={[{ name: 'brokers', active: true }]}
                className="TablePage"
            >
                <Row>
                    <Col xl={12} lg={12} md={12}>
                        <Card>
                            <CardHeader>To ngi mi gii</CardHeader>
                            <CardBody>
                                <Formik
                                    initialValues={
                                        {
                                            name: this.props.detail.broker.name,
                                            email: this.props.detail.user.email,
                                            password: "",
                                            phone: this.props.detail.user.phone,
                                            introduce: this.props.detail.broker.introduce,
                                            company: JSON.stringify(this.props.detail.broker.company),
                                        }
                                    }
                                    validationSchema={createSchema }
                                    onSubmit={async values => {
                                        let data ={name:values.name,email:values.email,phone: values.phone,introduce:values.introduce, dob: values.dob, company:values.company};
                                        if(values.password){
                                            Object.assign(data,{password: values.password})
                                        }
                                        await this.props.putBroker(this.props.match.params.id,data);
                                        history.push('/brokers/'+this.props.match.params.id+'?success=true');
                                    }}
                                >
                                    {({ errors, touched, values, handleChange,handleBlur, setFieldValue }) => (
                                        <Form>
                                            {errMsg}
                                            <FormGroup>
                                                <Label for="name">Tn</Label>
                                                <Input
                                                    type="text"
                                                    name="name"
                                                    onChange={evt => {
                                                        let value = evt.target.value;
                                                        if(value){
                                                            value = value.toLowerCase();
                                                        }
                                                        setFieldValue(
                                                            "name",
                                                            value
                                                        )
                                                    }
                                                    }
                                                    onBlur={handleBlur}
                                                    placeholder="Tn ngi mi gii"
                                                    value={values.name}
                                                />
                                                {errors.name && touched.name ? (
                                                    <UncontrolledAlert color="danger">
                                                        {errors.name}
                                                    </UncontrolledAlert>
                                                ) : null}
                                            </FormGroup>
                                            <FormGroup>
                                                <Label for="name">in thoi</Label>
                                                <Input
                                                    type="text"
                                                    name="phone"
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    placeholder="in thoi mi gii"
                                                    value={values.phone}
                                                />
                                                {errors.phone && touched.phone ? (
                                                    <UncontrolledAlert color="danger">
                                                        {errors.phone}
                                                    </UncontrolledAlert>
                                                ) : null}
                                            </FormGroup>
                                            <FormGroup>
                                                <Label for="email">Email</Label>
                                                <Input
                                                    type="email"
                                                    name="email"
                                                    onChange={evt => {
                                                        let value = evt.target.value;
                                                        if(value){
                                                            value = value.toLowerCase();
                                                        }
                                                        setFieldValue(
                                                            "email",
                                                            value
                                                        )
                                                    }
                                                    }
                                                    onBlur={handleBlur}
                                                    placeholder="Email Ngi mi gii"
                                                    value={values.email}
                                                />
                                                {errors.email && touched.email ? (
                                                    <UncontrolledAlert color="danger">
                                                        {errors.email}
                                                    </UncontrolledAlert>
                                                ) : null}
                                            </FormGroup>
                                            <FormGroup>
                                                <Label for="password">Password</Label>
                                                <Input
                                                    type="password"
                                                    name="password"
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    placeholder="Mt khu"
                                                    value={values.password}
                                                />
                                                {errors.password && touched.password ? (
                                                    <UncontrolledAlert color="danger">
                                                        {errors.password}
                                                    </UncontrolledAlert>
                                                ) : null}
                                            </FormGroup>
                                            <FormGroup>
                                                <Label for="repassword">Nhp li password</Label>
                                                <Input
                                                    type="password"
                                                    name="repassword"
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    placeholder="Mt khu"
                                                    value={values.repassword}
                                                />
                                                {errors.repassword && touched.reassword ? (
                                                    <UncontrolledAlert color="danger">
                                                        {errors.repassword}
                                                    </UncontrolledAlert>
                                                ) : null}
                                            </FormGroup>
                                            <FormGroup>
                                                <Label for="introduce">Gii thiu</Label>
                                                <Input
                                                    type="textarea"
                                                    name="introduce"
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    placeholder=""
                                                    value={values.introduce}
                                                />
                                                {errors.introduce && touched.introduce ? (
                                                    <UncontrolledAlert color="danger">
                                                        {errors.introduce}
                                                    </UncontrolledAlert>
                                                ) : null}
                                            </FormGroup>

                                            <FormGroup>
                                                <Label for="company">Cng ty</Label>
                                                <br></br>
                                                {this.props.detail&&this.props.detail.broker&&this.props.detail.broker.company?this.props.detail.broker.company.name:null}
                                                <br></br>
                                                <Select
                                                    name="company"
                                                    value={values.company}
                                                    onChange={(value) => {
                                                        if(value){
                                                            value = JSON.parse(value.value);
                                                        }
                                                        setFieldValue(
                                                            "company",
                                                            value
                                                        )
                                                    }
                                                    }
                                                    onBlur={handleBlur}
                                                    options={options}
                                                />
                                            </FormGroup>
                                            <FormGroup>
                                                <Label for="dob">Ngy sinh</Label>
                                                <Input type="text" name="dob"
                                                       onChange={handleChange}
                                                       onBlur={handleBlur}
                                                       value={values.dob}
                                                >
                                                </Input>
                                            </FormGroup>
                                            <Button color="success" type='submit'>Gi</Button>

                                        </Form>
                                    )}

                                </Formik>
                            </CardBody>
                        </Card>
                    </Col>

                </Row>
            </Page>
        );
    }
}

export default connect((state) => {
    return  {
        companies: state.companies.list_company,
        detail: state.brokers.broker_detail,
        error: state.common.requests.error,
    }
}, {getListCompanies,putBroker})(BrokerEditPage);
