import {
    LIST_JOB_SUCCESS,
    DETAIL_JOB_SUCCESS,
    DELETE_JOB_SUCCESS,
    UPDATE_JOB_SUCCESS,
    CREATE_JOB_SUCCESS,
    SEARCH_JOB_SUCCESS,
    ACTIVE_JOB_SUCCESS
} from '../constants/jobs';

import {BASEURL} from "config/constant";
import {API_REQUEST} from '../constants/actions';

export const listAddresses = (page = 1, limit = 100, coin, address = "") => ({
    type: API_REQUEST,
    payload: {
        url: BASEURL + 'api/v1/addresses',
        method: 'GET',
        params: { page, limit, address, coin },
        success: res => {
            return ({
                type: "API_FINISH",
                payload: res.data
            })
        }
    }
});

export const scanBalance = (coin) => ({
    type: API_REQUEST,
    payload: {
        url: BASEURL + 'api/v1/addresses/scan-balance',
        method: 'GET',
        params: { coin },
        success: res => {
            return ({
                type: "API_FINISH",
                payload: res.data
            })
        }
    }
});

export const transferBalance = (coin, address) => ({
    type: API_REQUEST,
    payload: {
        url: BASEURL + 'api/v1/addresses/transfer-balance',
        method: 'GET',
        params: { coin, address},
        success: res => {
            return ({
                type: "API_FINISH",
                payload: res.data
            })
        }
    }
});
